
import React from 'react';
import './App.css';
import { Switch, Route } from 'react-router-dom';
import Homepage from './components/Homepage.component/Homepage.jsx';
import Portfolio from './components/Portfolio.component/portfolio.jsx';
import Skills from './pages/skills/skills.component.jsx';
import About  from  './pages/about/about.component.jsx';
import Professional_summary from  './pages/professional_summary/professional_summary.component.jsx';
import Projects from  './pages/projects/projects.component.jsx';
import ContactPage from './pages/contact-page/contact-page.component.jsx';



function App() {
  return (
    <div className="App container">
    <Switch>
      <Route exact path='/' component={Homepage} />
      <Route exact path='/portfolio_page' component={Portfolio} />
      <Route path='/portfolio_page/#about' component={About} />
      <Route path='/portfolio_page/#my_skills' component={Skills} />
      <Route path='/portfolio_page/#professional-summary' component={Professional_summary} />
      <Route path='/portfolio_page/#my_projects' component={Projects} />
      <Route path='/portfolio_page/#contact-me' component={ContactPage}/>
    </Switch>
    </div>
  );
}

export default App;
